<template>
  <div>
        <v-alert type="info" icon="mdi-checkbox-multiple-marked-outline" border="left" class="my-0" tile>
          <h4 class="text-center">ICT Training for Youths in Ekiti State</h4>
        </v-alert>
    <v-card v-if="showForm" tile class="px-2">
      <v-row justify="center">
        <v-col cols="10" sm="6" md="4" lg="3" xl="2">
          <v-img
            :src="`/uploads/interns/${dform.passport}`"
            height="150"
            width="150"
            contain
            class="mx-auto"
          >
            <template #placeholder>
              <v-skeleton-loader
                v-bind="attrs"
                type="card-avatar, image"
              ></v-skeleton-loader>
            </template>
          </v-img>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="10" sm="6" md="4" lg="3">
          <v-file-input
            accept="image/*"
            label="Upload Passport (<50KB)"
            :rules="rules.img"
            @change="handlePassport"
            v-if="!dform.passport || edit.passport"
          ></v-file-input>
          <p v-else class="my-0 text-center">
            <v-btn
              small
              class="mx-auto"
              color="primary"
              @click="edit.passport = true"
              >Change Image</v-btn
            >
          </p>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="8" sm="4" md="3" lg="1">
          <p v-if="edit.passport" class="my-0 text-center">
            <v-btn
              small
              class="mx-auto"
              color="primary"
              @click="edit.passport = false"
              >Cancel Image Change</v-btn
            >
          </p>
        </v-col>
      </v-row>

      <v-row class="mt-2">
        <v-col cols="12" md="6" lg="4">
          <v-text-field
            v-model="dform.surname"
            label="Surname:"
            outlined
            dense
          >
          </v-text-field>
        </v-col>
        <v-col cols="12" md="6" lg="4">
          <v-text-field
            v-model="dform.firstname"
            label="First Name:"
            outlined
            dense
          >
          </v-text-field>
        </v-col>
        <v-col cols="12" md="6" lg="4">
          <v-text-field
            v-model="dform.middlename"
            label="Middle Name:"
            outlined
            dense
          >
          </v-text-field>
        </v-col>
				<v-col cols="12" md="6" lg="4">
          <v-text-field
            v-model.trim="dform.phoneno"
            maxlength="11"
            counter="11"
            label="Mobile Number:"
            outlined
            dense
          >
          </v-text-field>
        </v-col>
				<v-col cols="12" md="6" lg="4">
          <v-text-field
            v-model.trim="dform.email"
            label="E-mail:"
            outlined
            dense
          >
          </v-text-field>
        </v-col>
        <v-col cols="12" md="6" lg="4">
          <v-select
            v-model="dform.gender"
            :items="genders"
            label="Gender:"
            outlined
            dense
            required
          >
          </v-select>
        </v-col>
				<v-col cols="12" md="6" lg="4">
          <v-textarea
            v-model="dform.resident_address"
            label="Residential Address:"
            outlined
            dense
            auto-grow
            rows="1"
          >
          </v-textarea>
        </v-col>        
        <v-col cols="12" md="6" lg="4">
          <v-select
            v-model="dform.qualification"
            label="Highest Educational Qualification:"
            outlined
            dense
            :items="qualifications"
          >
          </v-select>
        </v-col>
        
        <v-col cols="12" md="6" lg="4">
          <v-text-field
            v-model="dform.discipline"
            label="Course of Study:"
            outlined
            dense
          >
          </v-text-field>
        </v-col>
        <v-col cols="12" md="6" lg="4">
          <v-select
            v-model="dform.religion"
            :items="religions"
            label="Religion:"
            outlined
            dense
            required
          >
          </v-select>
        </v-col>
        <v-col cols="12" md="6" lg="4">
          <v-radio-group v-model="dform.laptop" row>
            <span>Do you have a personal laptop?</span>
            <v-radio label="Yes" color="red" value="Yes" class="mx-2"></v-radio>
            <v-radio label="No" color="red" value="No" class="mx-2"></v-radio>            
          </v-radio-group>
        </v-col>				
        <v-col cols="12" md="6" lg="4">
          <v-radio-group v-model="dform.available" row>
            <span>Will you be available for the training: (Monday - Friday, 8am - 4pm)?</span>
            <v-radio label="Yes" color="red" value="Yes" class="mx-2"></v-radio>
            <v-radio label="No" color="red" value="No" class="mx-2"></v-radio>            
          </v-radio-group>
        </v-col>
				<v-col cols="12" md="6" lg="4">
          <v-select
            v-model="dform.program"
            :items="programs"
            label="Select Preferred Program for Training:"
            outlined
            dense
            required
          >
          </v-select>
        </v-col>
				<v-col cols="12" md="6" lg="8">
          <v-file-input
            label="Upload CV (PDF)"
            outlined
            append-icon="mdi-file-pdf-outline"
            dense
            accept="application/pdf"
            :rules="rules.file"
            show-size
            v-if="!dform.cv|| edit.cv"
            @change="handleCV"
          >
          </v-file-input>
          <div class="text-center" v-else>
            <b>Curriculum Vitae: </b>
            <v-icon size="4rem">mdi-file-pdf-outline</v-icon>
            <v-btn
              color="primary"
              small
              shaped
              @click="edit.cv = true"
              class="mr-1"
              >change file</v-btn
            >
            <v-btn
              :href="`/uploads/interns/${dform.cv}`"
              download
              small
              shaped
              >download file</v-btn
            >
          </div>
          <div class="text-center" v-if="edit.cv">
            <v-btn color="primary" small shaped @click="edit.cv = false"
              >cancel change file</v-btn
            >
          </div>
        </v-col>				
      </v-row>
      <v-row justify="center" align="center">
      <v-btn
        shaped
        color="primary"
        @click="submitApplication"
        class="mb-5"
        :disabled="!valid"
        :loading="loading"
      >
        Submit Application
      </v-btn>
    </v-row>
    </v-card>
		<v-alert v-else prominent type="success" border="left">
      <v-row align="center">
        <v-col class="grow">
          Application Submitted Successfully. Your application number is <b>{{ application_no }}</b>. An email has been sent to you. Check your inbox or spam. You can click the close button.
        </v-col>
        <v-col class="shrink">
          <v-btn to="/" color="primary">close</v-btn>
        </v-col>
      </v-row>
    </v-alert>
    <v-alert type="info" icon="mdi-checkbox-multiple-marked-outline" border="left" class="my-0" tile>
      <h4 class="text-center"><b>Sponsor(s)</b></h4>
        <h5 class="text-center">Hon. Akin Oyebode, Commissioner for Finance and Economic Development, Government of Ekiti State, Nigeria.</h5>
      </v-alert>
    <v-snackbar v-model="snackbar" :color="snackColor" bottom :timeout="5000">
      {{ snackMsg }}

      <template v-slot:action="{ attrs }">
        <v-btn color="dark" v-bind="attrs" @click="snackbar = false">
          <v-icon>mdi-close</v-icon>
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import { apiClient } from "@/services";
import { snackMixin } from "@/mixins";
import { mapState } from "vuex";
export default {
  name: "Interns",
  mixins: [snackMixin],
  data() {
    return {
      edit: { passport: false, cv: false },
      showForm: true,
      valid: true,
      application_no: '',
      attrs: {
        class: "mb-6",
        boilerplate: true,
        elevation: 2,
      },      
      dform: {
        id: "",
        application_no: "",
        surname: "",
        firstname: "",
        middlename: "",
        phoneno: "",
        email: "",
        gender: "",
        resident_address: "",
        qualification: "",
        discipline: "",
        religion: "",
        laptop: "",
        available: "",
        program: "",
        passport: "",
        cv:"",
      },
      rules: {
        img: [
          (v) => !v || v.type.includes("image/") || "Upload an image file",
          (v) => !v || v.size < 52000 || "Image size should be less than 50KB"
        ],
        file: [
          (v) => !v || v.type.includes("application/pdf") || "Upload a PDF file"
        ],
      },
    };
  },
  computed: {
    ...mapState(["genders","religions", "qualifications", "programs"]),
  },
  methods: {    
    submitApplication() {
      this.loading = true;
      // Do Validation here
      apiClient
        .post("/interns", this.dform)
        .then((res) => {
          // console.log(res.data);
          if (res.data.ok) {
            this.showForm = false;
            this.application_no = res.data.application_no;
            this.displayMsg("Application Submitted");
          } else {
            this.displayMsg(res.data, "error");
          }
        })
        .catch((err) => this.displayMsg(err.message, "error"));
    },
    handlePassport(file) {
      if (!file) {
        return;
      }
      const validImage = file.type.includes("image/") && file.size < 52000;
      if (!validImage) {
        this.displayMsg("Invalid Passport Image!", "error");
        return;
      }
      this.processFile(file, 'passport');
    },
    handleCV(file) {
      if (!file) {
        return;
      }
      const validFile = file.type.includes("application/pdf");
      if (!validFile) {
        this.displayMsg("Invalid CV file!", "error");
        return;
      }
      this.processFile(file, 'cv');          
    },
    processFile(file, filename){
      let fd = new FormData();
      let data = { filename: this.dform[filename] };
      fd.append("file", file);
      fd.append("data", JSON.stringify(data));
      let config = { header: { "Content-Type": "multipart/form-data" } };
      apiClient
        .post("/process_file", fd, config)
        .then((res) => {
          // console.log(res.data);
          if (res.data.filename) {
            this.dform[filename] = res.data.filename;
            this.edit[filename] = false;
            this.displayMsg("File uploaded successfully.");
          } else {
            this.displayMsg(res.data, "error");
          }
        })
        .catch((err) => this.displayMsg(err.message, "error"));
    }
  },
};
</script>
